
import fs from "fs/promises"
import { fileURLToPath } from "url";
import path from "path";

// Chuyá»ƒn Ä‘á»•i import.meta.url sang __dirname
const __filename = fileURLToPath(import.meta.url);
const __dirname = path.dirname(__filename);
const files = [
    path.join(__dirname, "../src/coin_list/coin_list.json"),

];

export const searchCoinInFileJsonProvider = async(coinSymbol:string)=>{
    const results = await Promise.all(
        files.map(async (file) => {
            try {

                const data = await fs.readFile(file, 'utf8');
                const coins = JSON.parse(data);

                // TÃ¬m kiáº¿m coin
                const foundCoin = coins.find(
                    coin =>
                        coin.symbol.toLowerCase() === coinSymbol.toLowerCase()
                );

                return foundCoin || null; // Tráº£ vá» káº¿t quáº£ hoáº·c null
            } catch (err) {
                console.error(`Error reading file ${file}:`, err);
                return null; // Tráº£ vá» null náº¿u xáº£y ra lá»—i
            }
        })
    );

    // Lá»c káº¿t quáº£ Ä‘á»ƒ tÃ¬m pháº§n tá»­ Ä‘áº§u tiÃªn khÃ´ng null
    return results.find(result => result !== null) || null;
}
// Module exports
export const searchCoinInFileJsonProvider2 = async(coinSymbol:string, coinName:string)=>{
    const results = await Promise.all(
        files.map(async (file) => {
            try {

                const data = await fs.readFile(file, 'utf8');
                const coins = JSON.parse(data);

                // TÃ¬m kiáº¿m coin gÃ¢n Ä‘Ãºng
                const foundCoin = coins.find(
                    coin =>
                        (coin.symbol.toLowerCase() === coinSymbol.toLowerCase()||
                    coin.name.toLowerCase() === coinName.toLowerCase()) &&
                    coin.verified === true
                );

                return foundCoin || null; // Tráº£ vá» káº¿t quáº£ hoáº·c null
            } catch (err) {
                console.error(`Error reading file ${file}:`, err);
                return null; // Tráº£ vá» null náº¿u xáº£y ra lá»—i
            }
        })
    );

    // Lá»c káº¿t quáº£ Ä‘á»ƒ tÃ¬m pháº§n tá»­ Ä‘áº§u tiÃªn khÃ´ng null
    return results.find(result => result !== null) || null;
}
